@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body , html{
  overflow-x: hidden !important;
}

code {
  font-family: 'Montserrat', sans-serif;
}


/* Header.js */
.header{
    position: fixed;
    top: 0%;
    left: 0%;
    right: 0%;
    z-index: 2;
}

/* for header count */
.cart-count{
    background-color: deepskyblue;
    border-radius:5px;
    
}




.navbar{
    background-color: #5c33a0 !important;
}

.nav-link{
    color: white !important;
}

.navbar-nav{
    margin-right: 20px;
}

.navbar-brand{
    color: white !important;
    margin-left: 1 px;
    font-weight: bold;
}


/* Layout.js */
.content{
    padding-top: 100px;
    /* background-color:aliceblue !important; */
}


/* footer.js */
.container .scroll{
    max-width: 100%;
    background-color: white !important;
    -webkit-text-decoration: aliceblue;
            text-decoration: aliceblue;
    padding: 100px;
    text-align: justify;
    position: absolute;
    bottom: 0%;
    left: 0%;
    right: 0%;


    /* for Scrollbar */
    margin:4px, 4px;
    width: 500px;
    height: 110px;
    overflow: auto;
}


.footer{
    padding: 10px;
    text-align: center;
    background-color:#744eb0;   
    color:white;
    position: fixed !important;
    bottom:0;
    left: 0;
    right: 0;
    width: 100%;
    height: 50px !important;
  
}



.button:hover{
    background-color:#5C33A0;
}



/* for Loading a Loader on the page for Homepage->Layout Component */
/* class from Layout.js */
.loader{
    position: absolute;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 99999;
}

.spinner-border{
    height: 100px;
    width: 100px;
}



a{
    text-decoration: none;
}




/* for search & filter */

.input,select{
    height: 35px !important;
}




/* Admin Pannel tabs Styling */
.nav-item button{
    color:black !important;
}



.nav-tabs{
    background-color:cadetblue;
}

 .product-img{
    height: 240px;
    width: 200px;
}

.product p{
    min-height: 3rem;
}

.product{
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}


.product:hover .product-actions{
    opacity: 1;
}
.product-actions{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    opacity: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color:rgba(0, 0, 0, 0.8);
    color: white;
    font-weight: bold !important;
}

.image-effect{
    overflow: hidden;
    margin: 0 auto;
    /* object-fit: cover; */
}

.product-info-img{
    height: 70vh;
    width: 100%;
    transition: 0.5s all ease-in-out;
    display: block !important;
    /* object-fit: cover !important;  */


}

.product-info-img:hover{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
}



/* For CartPage.js(for Styling of Total amount only) */
.total-amount{
    background: linear-gradient(45deg, #5a33a063, #fad0c4);
  border-radius: 41px;
  padding: 12px;
  display: inline-block;
  color: #292626;
  font-weight: bold;
}

.cartOrder{
    box-shadow:rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset;
    border-bottom-color: 5px,solid,black;
    background-color:aliceblue;
}



/* for Order page table */
.order{
    background-color:aliceblue;
    box-shadow:rgba(0, 0, 0, 0.17) 0px -23px 25px 0px inset;
    border-bottom-color: 5px,solid,black;
}

.ordertable{
    
}
/* All Styling Related to Login and registration goes here */


/* FOR REGISTER Page */
.form-control{
    box-shadow: none;
}

.form-control:focus{
    box-shadow:none !important;
}

input, textarea{
    margin-top: 15px;
}




.register-form{
    /* width: 100%; */
    border-radius: 25px;
    padding: 20px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    background-color: whitesmoke;
    /* margin-left: 70px !important;
    margin-right: 70px !important; */

}



.register-parent{
    min-height: 100vh;
    display: flex;
    align-items: center;
    background-color: aliceblue;
}

.register-top{
    height: 50%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    background-color:#5c33a0;
}

.z1{
    z-index: 1 !important;
}


/* ====================================================================================================== */

/* FOR LOGIN Page */


.login-form{
    border-radius: 25px;
    padding: 20px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    background-color: whitesmoke;
}


.login-parent{
    min-height: 100vh;
    display: flex;
    align-items: center;
    background-color: aliceblue;
}

.login-bottom{
    height: 50%;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    background-color:#5c33a0;
}

.z1{
    z-index: 1 !important;
}
